.nav {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;

    display: flex;
    justify-content: space-between;
}

.nav_logo {
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}

.nav_avatar {
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}